<template>
  <div id="main-cont">
    <!----------------CHAT--------------->
    <audio
      ref="audio"
      id="audio"
      controls="false"
      style="display: none"
      src="./assets/notification.mp3"
    ></audio>
    <!------------------------------------>
    <div v-if="token && !chatPage" @click="gotoChat" class="msgs">
      <a>
        <span></span>
        <i class="fa fa-comment">{{ msgNumber }}</i>
      </a>
    </div>
    <!----------------CHAT-END--------------->
    <div v-if="ready" id="all">
      <v-app id="inspire">
        <router-view name="header" />
        <transition name="slide" mode="out-in">
          <div class="page">
            <router-view :key="routerKey"></router-view>
          </div>
        </transition>
        <!-- <app-footer /> -->
      </v-app>

      <app-modal />
      <app-message />
      <div v-if="live" class="live-notification">
        <div class="box">
          <router-link to="/live-video" class="navbar-brand live"> LIVE </router-link>
        </div>
      </div>
    </div>
    <loading-page v-if="!ready" />
  </div>
</template>

<script>
import axios from "axios";
import AppMessage from "./components/message.vue";
import AppFooter from "./components/footer.vue";
// import socket from "socket.io-client";
import LoadingPage from "./components/loading_page.vue";
//////////////////CHAT////////////////////
import { url, socket } from "./main.js";
//////////////////CHAT-END////////////////////

export default {
  async created() {
    await this.$store.dispatch("initApp");
    this.$store.dispatch("generateRouterKey");
    if (this.role == 0) {
      axios.get("/addVisitor").then((res) => {});
    }

    //////////////////CHAT////////////////////
    socket().on("reconnect", () => {
      if (this.role == 0) socket().emit("joinRoom", this.$store.getters.userId);
      if (this.role == 1) socket().emit("joinAdminRoom");
    });
    if (this.role == 0) socket().emit("joinRoom", this.$store.getters.userId);
    if (this.role == 1) socket().emit("joinAdminRoom");
    //////////////////CHAT-END////////////////////

    // this.io.on("live", () => {
    //   this.live = !this.live;
    // });

    // this.io.on("finish", () => {
    //   this.live = false;
    // });
    //////////////////////////////////////
    ///////////////////////////////////////
  },
  mounted() {
    //////////////////CHAT////////////////////
    socket().on("receiveMessage", (mess) => {
      if (mess.senderId != this.$store.getters.userId) {
        const audio = document.getElementById("audio");
        audio.play();
        if (this.chatPage) return;
        const msgNumber = this.$store.getters.msgNumber;
        const inc = +msgNumber + 1;
        this.$store.dispatch("changeMsgNumber", inc);
      }
    });
    //////////////////CHAT-END////////////////////
  },
  data() {
    return {
      // io: socket(axios.defaults.baseURL),
      live: false,
      chatPage: false,
    };
  },
  computed: {
    ready() {
      return this.$store.getters.ready;
    },
    routerKey() {
      return this.$store.getters.routerKey;
    },
    token() {
      return this.$store.getters.token;
    },
    role() {
      return this.$store.getters.role;
    },
    //////////////////CHAT////////////////////
    msgNumber() {
      return this.$store.getters.msgNumber;
    },
    //////////////////CHAT-END////////////////////
  },
  watch: {
    //////////////////CHAT////////////////////
    $route(to) {
      if (to.path.includes("chat")) this.chatPage = true;
      else this.chatPage = false;
    },
    //////////////////CHAT-END////////////////////
  },
  name: "app",
  components: {
    AppFooter,
    LoadingPage,
    AppMessage,
  },
  methods: {
    //////////////////CHAT////////////////////
    gotoChat() {
      if (this.role == 0) this.$router.push("/chat");
      else if (this.role == 1) this.$router.push("/dashboard/chat");
    },
    //////////////////CHAT-END////////////////////
  },
};
</script>

