import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import Vuelidate from 'vuelidate'
import axios from 'axios'
import vuetify from './plugins/vuetify';
import VueMoment from 'vue-moment'
import Loading from "./components/loading"
import ResponsiveTable from "./components/responsive-table"
import Progress from "./components/progress-bar"
import Modal from "./components/modal"
import Filter from "./components/filter"
import { Capacitor } from '@capacitor/core';
import io from "socket.io-client";


import 'material-design-icons-iconfont/dist/material-design-icons.css'
import "../node_modules/bootstrap/dist/css/bootstrap.css"
import "../node_modules/jquery/dist/jquery.min.js"
import "../node_modules/popper.js/dist/popper.min.js"
import "../node_modules/bootstrap/dist/js/bootstrap.js"


import './assets/styles.scss'
////////////////////////////////////////////////////////////////

let url1 = "";
let socketIo;
(async function testUrl() {
  const setUrl = () => new Promise(async (resolve) => {
    let url = process.env.VUE_APP_URL;
    if(Capacitor.isNative ) {
      try {
        url = process.env.VUE_APP_URL;
        const res = await axios.get(url1 + "/getAdminData");
      } catch(err) {
        url = process.env.VUE_APP_URL2;
      }
    }
    else url =  process.env.NODE_ENV == "production"? window.location.origin: "http://localhost:5000";
    resolve(url);
  })
  
  url1 = await setUrl();
  //////////////////CHAT////////////////////
  socketIo = io(url1);
  //////////////////CHAT////////////////////
  axios.defaults.baseURL = url1;
  
  Vue.component("loading", Loading);
  Vue.component("responsive-table", ResponsiveTable);
  Vue.component("progress-bar", Progress);
  Vue.component("app-filter", Filter);
  Vue.component("app-modal", Modal);
  
  
  Vue.filter("stage", val => {
    let stage = '';
    switch(val) {
      case "1": stage = '  ';
      break;
      case "2": stage = '  ';
      break;
      case "3": stage = '  ';
      break;
      case "4": stage = '  ';
      break;
      case "5": stage = '  ';
      break;
      case "6": stage = '  ';
      break;
    }
    return stage;
  });
  
  Vue.filter("number", val => {
    const Enum = val.toString().split("");
    const e = ['', '', '', '', '', '', '', '', '', ''];
    const Anum = Enum.map(n => {
      return e[+n];
    })
    return Anum.join("");
  });
  
  Vue.filter("section", (val, stage) => {
    const section = store.getters.sections.find((sec) => sec.number == val && sec.stage == stage);
    if(section) {
      return section.name;
    } else return val;
  });
  
  Vue.filter("student", (val, prop) => {
    const student = store.getters.students.find((user) => user._id == val);
    if(student) {
      return student[prop];
    } else return val;
  });
  
Vue.filter("truncate", val => {
  if(val.length < 20) return val;
  const chuncks = val.match(/.{1,20}/g);
  return chuncks.join(" ");
});
  
  Vue.use(Vuelidate);
  Vue.use(VueMoment);

  axios.defaults.headers.common['authorization'] = localStorage.getItem('token');
  Vue.config.productionTip = false
  
  new Vue({
    router,
    store,
    vuetify,
    render: h => h(App)
  }).$mount('#app')
  
})()

//////////////////CHAT////////////////////
export const socket = () => socketIo;
//////////////////CHAT////////////////////
export const url = () => url1;
export const eventBus = new Vue();


