import axios from 'axios';

  const state = {
    pageData: {
      currentPage: 0,
      itemsPerPage: 2
    },
    order: '',
    showConfirmModal: false,
    wishloading: false,
    adminData: {},
    fullDegree: 60,
    order_modal: false,
    sections: [],
    students: [],
    exams: [],
    ready: false,
    stages: [
      {text: "  ", value: 1},
      {text: "  ", value: 2},
      {text: "  ", value: 3},
      {text: "  ", value: 4},
      {text: "  ", value: 5},
      {text: "  ", value: 6},
    ],
    routerKey: 1,
    msgNumber: 0,
  };



  const mutations = {
    putAdminData(state, data) {
      if(data) {
        state.adminData = data;
      }
    },
  };



  const actions = {
    getExamsFullDegree({rootState, commit}, data) {
      axios.get('/getStageStudentsOrder/'+data.stage+'/'+data.type).then(res => {
        state.fullDegree = res.data.fullDegree;
      })
    },
    async getSections({commit}) {  
      const res = await axios.get('/getSections');
      state.sections = res.data.sections;
    },
    async getUsers({commit}) {
      let method;
      // if(localStorage.getItem("role") == 0)
      //   method = axios.get('/getStageUsers/' + localStorage.getItem("stage"));
      // else if(localStorage.getItem("role") == 1)
        method = axios.get('/getUsers');
      const res = await method;
      state.students = res.data.users;
    },
    async getExams({commit}) {
      const res = await axios.get('/getExamsByStage/' + localStorage.getItem("stage"));
      state.exams = res.data.exams;

    },
    async getMsgs({commit, rootState}) {
      let res = {data: {messages: 0}};
      if(rootState.usersStore.role == 0) {
        res = await axios.get("/getUnseenMessagesNumber/" + rootState.usersStore.userId)
      }
      else if(rootState.usersStore.role == 1) {
        res = await axios.get("/getUnseenUsersMessagesNumber")
      }
      state.msgNumber = +res.data.messages;

    },

    generateRouterKey() {
      state.routerKey = Math.random().toString(36).substring(2, 5);
    },
    changeMsgNumber({}, number) {
      console.log(number);
      state.msgNumber = +number;
    },
     

    async initApp({commit, rootState, dispatch}) {
        await dispatch("getUsers");
        await dispatch("getSections");
        await dispatch("getExams");
        await dispatch("repairLocalStorage");
        await dispatch("isConfirmed");
        await dispatch("autoSignin");
      const res = await axios.get("/getAdminData");
      commit("putAdminData", res.data.adminData);
      await dispatch("getMsgs");
      state.ready = true;
    }
  };



  const getters = {
    adminData() {
      return state.adminData; 
    },
    fullDegree() {
      return state.fullDegree;
    },
    order_modal() {
      return state.order_modal;
    },
    sections() {
      return state.sections;
    },
    students() {
      return state.students;
    },
    exams() {
      return state.exams;
    },
    ready() {
      return state.ready;
    },
    stages() {
      return state.stages;
    },
    routerKey() {
      return state.routerKey;
    },
    msgNumber() {
      return +state.msgNumber;
    }
  }



  export default {
    state,
    mutations,
    actions,
    getters
  }
