<template>
  <div class="modall" :class="{ open: open }">
    <div class="modal-cont p-0">
      <div class="modal-header">
        <i
          @click="
            () => {
              open = false;
              component = null;
            }
          "
          class="fa fa-times"
        ></i>
      </div>
      <div v-if="component" class="modal-body p-2">
        <component :is="component.component" v-bind="{ ...component.props }" />
      </div>
    </div>
  </div>
</template>

<script>
import { eventBus } from "../main";
import Axios from "axios";
export default {
  created() {
    eventBus.$on("open-modal", (comp) => {
      this.component = comp;
      setTimeout(() => (this.open = true), 100);
    });
    eventBus.$on("close-modal", () => {
      setTimeout(() => (this.open = false), 100);
    });
  },
  data() {
    return {
      component: null,
      pdfUrl: null,
      open: false,
      pdf: null,
      loading: false,
      currentPage: 0,
      totalPages: 1,
    };
  },
};
</script>

