<template>
  <div v-if="inputs.length > 0">
    <div @click="toggleFilters" class="fixed-toggle">
      <i class="fa fa-bars"></i>
    </div>
    <!--------------------------------------->
    <div ref="menu" class="modal-menu border">
      <div class="row">
        <div class="col-md-12">
          <div class="filter p-1">
            <div v-for="(input, i) in inputs" :key="i" class="input">
              <div v-if="input">
                <div v-if="input.type == 'select'" class="form-group">
                  <label>{{ input.label }}</label>
                  <select
                    @change="
                      (e) =>
                        $emit('setFilter', {
                          filter: input.filter,
                          val: e.target.value.toLowerCase(),
                        })
                    "
                    class="form-control"
                    :value="input.value"
                    :readonly="input.readonly"
                  >
                    <option
                      v-for="(option, o) in input.options"
                      :key="o"
                      :value="option.value"
                    >
                      {{ option.text }}
                    </option>
                    <!-- <option value="">all</option> -->
                  </select>
                </div>
                <div v-if="input.type == 'date'" class="form-group">
                  <label>{{ input.label }}</label>
                  <input
                    @change="
                      (e) =>
                        $emit('setFilter', {
                          filter: input.filter,
                          val: e.target.value.toLowerCase(),
                        })
                    "
                    class="form-control"
                    type="date"
                    :value="input.value"
                    :readonly="input.readonly"
                  />
                </div>
                <div v-if="input.type == 'text'" class="form-group">
                  <label>{{ input.label }}</label>
                  <input
                    class="form-control"
                    type="text"
                    :value="input.value"
                    :readonly="input.readonly"
                    @change="
                      (e) =>
                        $emit('setFilter', {
                          filter: input.filter,
                          val: e.target.value.toLowerCase(),
                        })
                    "
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!------------------------------------------------>
      <div class="row g-3 mt-4 justify-content-center">
        <div class="col-md-2">
          <button
            @click="
              () => {
                $emit('filter');
                toggleFilters();
              }
            "
            class="btn btn-primary w-100"
          >
            filter
          </button>
        </div>
        <div class="col-md-2">
          <button @click="$emit('reset')" class="btn btn-danger w-100">
            reset filter
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["inputs"],
  methods: {
    toggleFilters() {
      const modal = this.$refs.menu;
      modal.classList.toggle("open");
    },
  },
};
</script>

