<template>
  <section class="mt-5">
    <!------------------------------------------------->
    <h4 class="_title">{{ title }}</h4>
    <div v-if="items.length == 0 && !loading" class="row justify-content-center">
      <div class="col-md-12">
        <h5 class="text-center">There is no items yet</h5>
      </div>
    </div>
    <!----------------->
    <div v-if="items.length > 0" class="">
      <div class="row px-md-2 _items">
        <div class="col-md-12">
          <div ref="item" v-for="(item, i) in oItems" :key="i" class="item">
            <div class="table-row">
              <div v-for="(title, c) in colsTitles" :key="c" class="">
                <div class="name _col">
                  <span>{{ title }}: </span>
                  <h5 v-if="colsTypes[c] == 'readonly'">{{ item[cols[c]] }}</h5>
                  <input
                    v-if="colsTypes[c] == 'input'"
                    type="text"
                    :value="round(item[cols[c]])"
                    @change="
                      (e) =>
                        $emit('tableInput', {
                          index: i,
                          key: cols[c],
                          val: e.target.value,
                        })
                    "
                  />
                </div>
              </div>
              <div v-if="btns && btns.length > 0" class="">
                <div class="table-btns">
                  <div v-for="(btn, b) in btns" :key="b" class="table-btn">
                    <i @click="btn.method(item, items)" :class="'fa fa-' + btn.icon"></i>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="footer" class="table-footer">
        <div class="d-flex flex-wrap justify-content-center align-items-center">
          <div>
            <span>{{ from }}</span>
            <span>-</span>
            <span>{{ to }}</span>
            <span>of</span>
            <span>{{ items.length }}</span>
          </div>
          <div>
            <span>
              <i @click="prev" class="fa fa-arrow-left"></i>
            </span>
            <span>
              <i @click="next" class="fa fa-arrow-right"></i>
            </span>
          </div>
          <div>
            <select @change="calcRows" v-model.number="rowsPerStep" class="form-control">
              <option value="10">10</option>
              <option value="30">30</option>
              <option value="50">50</option>
              <option value="100">100</option>
              <option value="1000">all</option>
            </select>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: [
    "title",
    "items",
    "loading",
    "cols",
    "colsTitles",
    "colsTypes",
    "btns",
    "color",
  ],
  created() {
    this.calcRows();
  },
  updated() {
    const items = this.$refs.item || [];
    if (items.length == 0) return;
    items.forEach((item, i) => {
      if (i % 2 == 0) {
        item.style.background = `linear-gradient(178deg, ${this.color}, ${this.color}ad )`;
      }
    });
  },
  data() {
    return {
      oItems: [],
      stepNumber: 0,
      rowsPerStep: 30,
      totalSteps: 0,
      ////////////////
      from: 0,
      to: 0,
    };
  },
  watch: {
    items(val) {
      this.calcRows();
    },
    stepNumber(val) {
      const from = (val - 1) * this.rowsPerStep;
      const to = val * this.rowsPerStep;
      this.oItems = [...this.items].slice(from, to);
      this.from = from + 1;
      this.to = from + this.oItems.length;
    },
  },
  computed: {
    footer() {
      if (this.oItems < 4) return false;
      else if (this.oItems > 3) return true;
    },
  },
  methods: {
    next() {
      if (this.stepNumber == this.totalSteps) return;
      this.stepNumber++;
    },
    prev() {
      if (this.stepNumber == 1) return;
      this.stepNumber--;
    },
    calcRows() {
      this.totalSteps = Math.ceil(this.items.length / this.rowsPerStep);
      if (this.items.length > 0) {
        // const number = this.stepNumber;
        // this.stepNumber = number == 0 ? 1 : number;
        this.stepNumber = 1;
        ////////////////////////////////
        const from = (this.stepNumber - 1) * this.rowsPerStep;
        const to = this.stepNumber * this.rowsPerStep;
        this.oItems = [...this.items].slice(from, to);
        this.from = from + 1;
        this.to = from + this.oItems.length;
      } else {
        this.oItems = [];
      }
    },
    round(val) {
      return Math.round(val);
    },
  },
};
</script>

